/* Calculator/Terminology Page Styles */
.calc-term-container {
  max-width: 80vw;
  margin: 0 auto;
  padding: 20px;
}

.tab-container {
  display: flex;
  margin-bottom: -1px; /* Overlap with content box */
}

.tab-button {
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: bold;
  background: #f0f0f0;
  border: none;
  border-top: 3px solid #ccc;
  position: relative;
  clip-path: polygon(0 0, 85% 0, 100% 100%, 15% 100%);
  margin: 0 -10px;
  z-index: 1;
  cursor: pointer;
  transition: all 0.3s ease;
}

.tab-button.active {
  background: white;
  border-top: 3px solid #6a5acd;
  color: #6a5acd;
  z-index: 2;
}

.content-box {
  background: white;
  border: 1px solid #ddd;
  border-radius: 0 5px 5px 5px;
  padding: 25px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  min-height: 300px;
  left: 3vw;
  width: 70vw;
  position: relative;
}

.content-page {
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.stat-table {
  border-collapse: collapse;
  border: 2px solid rgb(65, 2, 2);
  background-color: rgb(184, 182, 182);
  margin-bottom: 10px;
}

.stat-table th {
  /* Header cell styling */
  background-color: #282c34;
  color: white;
  text-align: center;
  width: 100px;
  border-bottom: 2px solid rgb(248, 247, 247); /* Horizontal divider */
  border-right: 2px solid #83a8f1; /* Vertical divider */
}

.stat-table td {
  
 
  border: 2px solid rgb(65, 2, 2);
}
